import BigNumber from 'bignumber.js'
import { iBigInt } from './utility.model'

export type BigNumberValue = BigNumber.Value | bigint

/**
 * Transform a value to BigNumber
 * @param value - the value to transform
 * @returns the big number
 */
export function BNify(value: BigNumberValue = 0): BigNumber {
  return new BigNumber(
    typeof value === 'bigint'
      ? value.toString()
      : typeof value === 'object'
      ? value
      : String(value)
  )
}

/**
 * Sum two bigInt values
 * @param a - the first value
 * @param b - the second value
 * @returns the sum as string
 */
export function BNsum(a?: iBigInt, b?: iBigInt): iBigInt {
  return BNify(a || 0)
    .plus(BNify(b || 0))
    .toFixed(0)
}

/**
 * Divide num by dev avoiding NaN
 * @param num numerator
 * @param den denominator
 * @returns num/den
 */
export function BNSafeDiv(num: BigNumberValue, den: BigNumberValue): BigNumber {
  if (BNeq(den)) return BNify(0)
  return BNify(num).div(BNify(den))
}

/**
 * Transform a value to BigNumber string
 * @param value - the value to transform
 * @returns the big number as string
 */
export function BNstring(value: BigNumberValue): iBigInt {
  return BNify(value).toString()
}

/**
 * Transform a value to BigNumber fixed to specified decimals
 * @param value - the value to transform
 * @returns the big number as fixed string
 */
export function BNFixed(value: BigNumberValue = 0, decimals = 0): iBigInt {
  return BNify(value).toFixed(decimals)
}

/**
 * Is BigInt greather than
 * @param bigInt - the bigint string
 * @param value - the value to compare
 * @returns true if greater
 */
export function BNgt(
  value1: BigNumberValue = 0,
  value2: BigNumberValue = 0
): boolean {
  return BNify(value1).gt(BNify(value2))
}

/**
 * Is BigInt greather than or equal
 * @param bigInt - the bigint string
 * @param value - the value to compare
 * @returns true if greater
 */
export function BNgte(
  value1: BigNumberValue = 0,
  value2: BigNumberValue = 0
): boolean {
  return BNify(value1).gte(BNify(value2))
}

/**
 * Is BigInt less than
 * @param bigInt - the bigint string
 * @param value - the value to compare
 * @returns true is less than
 */
export function BNlt(
  value1: BigNumberValue = 0,
  value2: BigNumberValue = 0
): boolean {
  return BNify(value1).lt(BNify(value2))
}

/**
 * Is BigInt less than or equal
 * @param bigInt - the bigint string
 * @param value - the value to compare
 * @returns true is less than or equal
 */
export function BNlte(
  value1: BigNumberValue = 0,
  value2: BigNumberValue = 0
): boolean {
  return BNify(value1).lte(BNify(value2))
}

/**
 * Is BigInt equal
 * @param bigInt - the bigint string
 * @param value - the value to compare
 * @returns true if equal
 */
export function BNeq(
  value1: BigNumberValue = 0,
  value2: BigNumberValue = 0
): boolean {
  return BNify(value1).eq(BNify(value2))
}

/**
 * Remove all decimals from bigNumberValue
 * @param value - the value to parse
 * @returns the string of the value
 */
export function BNint(value: BigNumberValue = 0): string {
  return BNify(value).integerValue(BigNumber.ROUND_FLOOR).toFixed(0)
}

/**
 * Return a number with the maximum number of significant decimals
 * @param value value to fix
 * @param decimals max decimals
 * @returns
 */
export function BNFloat(value: BigNumberValue = 0, decimals = 8): string {
  return parseFloat(BNify(value).toFixed(decimals)).toString()
}

/**
 * Return a number rounded up to a specific number of decimals
 * @param value value
 * @param decimals decimals
 * @returns rounded number
 */
export function BNRoundUpTo(value: BigNumber.Value, decimals: number): number {
  return BNify(value).decimalPlaces(decimals, BigNumber.ROUND_CEIL).toNumber()
}
